import { useState, useEffect, useCallback } from 'react';
import {
  LeaderboardResponse,
  LeaderboardTimeframe,
} from '../../data/models/Leaderboard';
import { LeaderboardService } from '../../domain/leaderboard/LeaderboardService';
import { Workout } from '../../data/models/Workout';

export function useLeaderboard(workouts: Workout[]) {
  const [leaderboard, setLeaderboard] = useState<LeaderboardResponse | null>(null);
  const [timeframe, setTimeframe] = useState<LeaderboardTimeframe>('weekly');
  const [displayName, setDisplayNameState] = useState<string | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const loadLeaderboard = useCallback(async () => {
    try {
      setIsLoading(true);
      setError(null);
      const data = await LeaderboardService.getLeaderboard(timeframe, workouts);
      setLeaderboard(data);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to load leaderboard');
      console.error('Error loading leaderboard:', err);
    } finally {
      setIsLoading(false);
    }
  }, [timeframe, workouts]);

  const loadDisplayName = useCallback(async () => {
    try {
      const name = await LeaderboardService.getDisplayName();
      setDisplayNameState(name);
    } catch (err) {
      console.error('Error loading display name:', err);
    }
  }, []);

  const updateDisplayName = useCallback(async (name: string) => {
    try {
      await LeaderboardService.setDisplayName(name);
      setDisplayNameState(name);
      await loadLeaderboard();
    } catch (err) {
      throw err;
    }
  }, [loadLeaderboard]);

  const changeTimeframe = useCallback((newTimeframe: LeaderboardTimeframe) => {
    setTimeframe(newTimeframe);
  }, []);

  const refresh = useCallback(async () => {
    await loadLeaderboard();
  }, [loadLeaderboard]);

  const generateSampleData = useCallback(async () => {
    try {
      await LeaderboardService.generateSampleData();
      await loadLeaderboard();
    } catch (err) {
      console.error('Error generating sample data:', err);
    }
  }, [loadLeaderboard]);

  useEffect(() => {
    loadDisplayName();
  }, [loadDisplayName]);

  useEffect(() => {
    loadLeaderboard();
  }, [loadLeaderboard]);

  return {
    leaderboard,
    timeframe,
    displayName,
    isLoading,
    error,
    updateDisplayName,
    changeTimeframe,
    refresh,
    generateSampleData,
  };
}
