import AsyncStorage from '@react-native-async-storage/async-storage';
import { LeaderboardUser, LeaderboardResponse, LeaderboardTimeframe } from '../models/Leaderboard';

const USER_ID_KEY = '@leaderboard_user_id';
const DISPLAY_NAME_KEY = '@leaderboard_display_name';
const LEADERBOARD_DATA_KEY = '@leaderboard_data';

export class LeaderboardStorage {
  /**
   * Generate or retrieve anonymous user ID
   */
  static async getUserId(): Promise<string> {
    try {
      let userId = await AsyncStorage.getItem(USER_ID_KEY);
      if (!userId) {
        userId = `anon_${Date.now()}_${Math.random().toString(36).substring(2, 15)}`;
        await AsyncStorage.setItem(USER_ID_KEY, userId);
      }
      return userId;
    } catch (error) {
      console.error('Error getting user ID:', error);
      throw error;
    }
  }

  /**
   * Get display name
   */
  static async getDisplayName(): Promise<string | null> {
    try {
      return await AsyncStorage.getItem(DISPLAY_NAME_KEY);
    } catch (error) {
      console.error('Error getting display name:', error);
      return null;
    }
  }

  /**
   * Set display name
   */
  static async setDisplayName(name: string): Promise<void> {
    try {
      await AsyncStorage.setItem(DISPLAY_NAME_KEY, name);
    } catch (error) {
      console.error('Error setting display name:', error);
      throw error;
    }
  }

  /**
   * Save user leaderboard data
   */
  static async saveUserData(user: LeaderboardUser): Promise<void> {
    try {
      const allData = await this.getAllLeaderboardData();
      const index = allData.findIndex(u => u.userId === user.userId);
      
      if (index >= 0) {
        allData[index] = user;
      } else {
        allData.push(user);
      }
      
      await AsyncStorage.setItem(LEADERBOARD_DATA_KEY, JSON.stringify(allData));
    } catch (error) {
      console.error('Error saving user leaderboard data:', error);
      throw error;
    }
  }

  /**
   * Get all leaderboard data (for local testing/demo)
   */
  static async getAllLeaderboardData(): Promise<LeaderboardUser[]> {
    try {
      const data = await AsyncStorage.getItem(LEADERBOARD_DATA_KEY);
      return data ? JSON.parse(data) : [];
    } catch (error) {
      console.error('Error getting leaderboard data:', error);
      return [];
    }
  }

  /**
   * Get user's own data
   */
  static async getCurrentUserData(): Promise<LeaderboardUser | null> {
    try {
      const userId = await this.getUserId();
      const allData = await this.getAllLeaderboardData();
      return allData.find(u => u.userId === userId) || null;
    } catch (error) {
      console.error('Error getting current user data:', error);
      return null;
    }
  }

  /**
   * Clear all leaderboard data (for testing)
   */
  static async clearAllData(): Promise<void> {
    try {
      await AsyncStorage.removeItem(LEADERBOARD_DATA_KEY);
    } catch (error) {
      console.error('Error clearing leaderboard data:', error);
      throw error;
    }
  }
}
