import { Workout } from '../../data/models/Workout';
import {
  LeaderboardUser,
  LeaderboardResponse,
  LeaderboardTimeframe,
  LeaderboardEntry,
} from '../../data/models/Leaderboard';
import { LeaderboardStorage } from '../../data/storage/LeaderboardStorage';
import { LeaderboardStatsCalculator } from '../../domain/leaderboard/LeaderboardStatsCalculator';

export class LeaderboardService {
  /**
   * Update user's leaderboard data after completing a workout
   */
  static async updateUserStats(workouts: Workout[]): Promise<void> {
    try {
      const userId = await LeaderboardStorage.getUserId();
      const displayName = (await LeaderboardStorage.getDisplayName()) || 'Anonymous';

      const stats = LeaderboardStatsCalculator.calculateUserStats(workouts);
      const score = LeaderboardStatsCalculator.calculateScore(stats);

      const userData: LeaderboardUser = {
        userId,
        displayName,
        stats,
        score,
        createdAt: Date.now(),
        lastUpdated: Date.now(),
      };

      await LeaderboardStorage.saveUserData(userData);
    } catch (error) {
      console.error('Error updating user stats:', error);
      throw error;
    }
  }

  /**
   * Get leaderboard for a specific timeframe
   */
  static async getLeaderboard(
    timeframe: LeaderboardTimeframe,
    workouts: Workout[]
  ): Promise<LeaderboardResponse> {
    try {
      const userId = await LeaderboardStorage.getUserId();
      
      // Filter workouts by timeframe for recalculation
      const filteredWorkouts = LeaderboardStatsCalculator.filterWorkoutsByTimeframe(
        workouts,
        timeframe
      );

      // For MVP, recalculate stats based on timeframe
      const stats = LeaderboardStatsCalculator.calculateUserStats(filteredWorkouts);
      const score = LeaderboardStatsCalculator.calculateScore(stats);

      // Get all users (in production, this would be a backend call)
      let allUsers = await LeaderboardStorage.getAllLeaderboardData();

      // Update current user's data
      const currentUserIndex = allUsers.findIndex(u => u.userId === userId);
      const currentUserData: LeaderboardUser = {
        userId,
        displayName: (await LeaderboardStorage.getDisplayName()) || 'Anonymous',
        stats,
        score,
        createdAt: currentUserIndex >= 0 ? allUsers[currentUserIndex].createdAt : Date.now(),
        lastUpdated: Date.now(),
      };

      if (currentUserIndex >= 0) {
        allUsers[currentUserIndex] = currentUserData;
      } else {
        allUsers.push(currentUserData);
      }

      // Sort by score descending
      allUsers.sort((a, b) => b.score - a.score);

      // Create leaderboard entries with ranks
      const leaderboard: LeaderboardEntry[] = allUsers.map((user, index) => ({
        rank: index + 1,
        userId: user.userId,
        displayName: user.displayName,
        score: user.score,
        stats: user.stats,
        isCurrentUser: user.userId === userId,
      }));

      // Find current user rank
      const currentUserRank = leaderboard.findIndex(e => e.userId === userId) + 1;

      return {
        timeframe,
        leaderboard,
        currentUserRank: currentUserRank > 0 ? currentUserRank : null,
        totalUsers: allUsers.length,
      };
    } catch (error) {
      console.error('Error getting leaderboard:', error);
      throw error;
    }
  }

  /**
   * Set user display name
   */
  static async setDisplayName(name: string): Promise<void> {
    if (!name || name.length < 2 || name.length > 20) {
      throw new Error('Display name must be between 2 and 20 characters');
    }

    const validNameRegex = /^[a-zA-Z0-9_\s]+$/;
    if (!validNameRegex.test(name)) {
      throw new Error('Display name can only contain letters, numbers, spaces, and underscores');
    }

    await LeaderboardStorage.setDisplayName(name);
  }

  /**
   * Get user display name
   */
  static async getDisplayName(): Promise<string | null> {
    return await LeaderboardStorage.getDisplayName();
  }

  /**
   * Check if user has set display name
   */
  static async hasDisplayName(): Promise<boolean> {
    const name = await LeaderboardStorage.getDisplayName();
    return name !== null && name.length > 0;
  }

  /**
   * Generate sample leaderboard data for testing
   */
  static async generateSampleData(): Promise<void> {
    const sampleNames = [
      'GymWarrior99',
      'IronLifter',
      'FitQueen',
      'SwolePatrol',
      'BeastMode',
      'LiftMaster',
      'PowerHouse',
      'FlexGod',
      'MuscleMania',
      'GainzTrain',
    ];

    for (let i = 0; i < sampleNames.length; i++) {
      const userData: LeaderboardUser = {
        userId: `sample_${i}`,
        displayName: sampleNames[i],
        stats: {
          totalWorkouts: Math.floor(Math.random() * 100) + 10,
          totalWeightLifted: Math.floor(Math.random() * 200000) + 50000,
          currentStreak: Math.floor(Math.random() * 30),
          longestStreak: Math.floor(Math.random() * 50) + 10,
          totalWorkoutTime: Math.floor(Math.random() * 10000000) + 1000000,
          personalRecords: Math.floor(Math.random() * 30) + 5,
        },
        score: 0,
        createdAt: Date.now() - Math.random() * 30 * 24 * 60 * 60 * 1000,
        lastUpdated: Date.now(),
      };

      userData.score = LeaderboardStatsCalculator.calculateScore(userData.stats);
      await LeaderboardStorage.saveUserData(userData);
    }
  }
}
